.jumboBackground {
    height: 100vh;
    width:100vw;
    background-color: rgb(0, 0, 0) !important;
    font: white;
}

.helloSize {
    font-size: 48px;
    color: white;
}

.helloDoubleArrowPaddingBottom {
    padding-bottom: 70px;
}

.centerHelloJumbotron {
    display: flex;
    align-content: center !important
}

.helloMargins {
    margin: auto;
    vertical-align: middle;
}

/* THIS NEEDS TO BE REVISED SO IT IS SMOOTH AS YOU STEP DOWN - CURRENTLY DOES NOT OVERFLOW */

@media screen and (max-width: 400px) {
    h1.helloSize {
      font-size: 12vw;
    }
  }

  @media screen and (max-width: 526px) {
    p.helloParagraph {
      font-size: 16px;
    }
  }

  @media screen and (max-width: 400px) {
    p.helloParagraph {
      font-size: 4vw;
    }
  }
